<template>
  <div id= "hello">
    <Head></Head>
    <Content></Content>
    <Footer></Footer>
  </div>
</template>

<script>
import Head from './head/headPage.vue';
import Content from './content/contentPage.vue';
import Footer from './footer/footerPage.vue';
export default {
  name: 'HelloWorld',
  components: {
      Head,
      Content,
      Footer
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  #hello{
    width:95%;
    height:91%;
    display: flex;
    justify-content: space-around;
    flex-direction: row-reverse;
    position: fixed;
    margin:auto;
    left:0;
    top:0;
    right:0;
    bottom:0;
  }
</style>



