<template>
    <div id= "content">
        <keep-alive>
            <router-view class="contentbox"></router-view>
        </keep-alive>
    </div>
</template>

<script>

export default {
    name: 'Content',
    mounted (){
        var self= this;
        $('#content').scroll(function () {
            //滚动条高度
            let scrollTop = $(this).scrollTop();
            //内容区域高度
            let scrollHeight = $('.contentbox').height();
            //窗口高度
            let clientHeight = $(this).height();

            if (scrollHeight - scrollTop <= clientHeight ) {
                self.$children[0].page++;
                if (self.$children[0].page >= self.$children[0].totalPage){
                    self.$children[0].page = self.$children[0].totalPage
                }
                                 
            }
            
        })  
    },
    computed: {
        getBack() {
        return this.$store.state.setUpPage.background;
        }
    },
    watch: {
        getBack(val) {
            $('.contentbox').css({
                "background-color": val
            })
            
        }
    }
}
</script>

<style lang="scss" scoped>
    #content{
        width:850px;
        height:600px;
        overflow: auto;
        border-radius: 6px;
       
       
    }
    ::-webkit-scrollbar { width: 0;}
    
    .contentbox{
        min-height:600px;
        background-color:#fff;
        animation: myfirst 0.5s;
    }
    @-webkit-keyframes myfirst 
    {
        from {
           transform: scale(0);
        }
        to {
            transform: scale(1);
        }
    }
    
</style>





