<template>
  <div id= "footer">
      <ul>
          <li v-for= "(item,i) in list" :key= "i" @click= "goList(item.href)">
              <i :class= "item.icon" :title= "item.title"></i>
          </li>
      </ul>
  </div>
</template>

<script>
export default {
    name: 'Footer',
    data () {
        return {
            list: [
                {
                    id: 0,
                    icon: 'iconfont icon-accountfilling',
                    title: '后台管理',
                    href: '/Looding'
                },{
                    id: 1,
                    icon: 'iconfont icon-msnui-list',
                    title: '文章列表',
                    href: '/list'
                },{
                    id: 2,
                    icon: 'iconfont icon-shezhi',
                    title: '设置',
                    href: '/setUp'
                
                }
                
            ]
        }
    },
    methods: {
        goList (href) {
            if (href) {
                this.$router.push({ path: href })
            }      
        }
    }
}
</script>

<style lang="scss" scoped>
    #footer{
        width:89px;
       
        ul{
            background:$background;
            border-radius: 6px;
            border:1px solid #696e71;
            li{
                cursor: pointer;
                display: flex;
                align-items: center;
                width:88px;
                height:88px;
                border-bottom:1px solid #5578a6;
                &:last-child{
                    border:0;
                }
                i{
                    margin:0 auto;
                    display: block;
                    width:50px;
                    height:50px;
                    border-radius: 50%;
                    background:#fff;
                    line-height: 50px;
                    text-align: center;
                    font-size:22px;
                    color:$background;
                }
                &:hover{
                    transition: all 1s;
                    background:#fff;
                    i{
                        background:$background;
                        color:#fff;
                    }
                    &:last-child{
                        border-radius: 0 0 6px 6px;
                    }
                    &:first-child{
                        border-radius: 6px 6px 0 0;
                    }
                }
            }
        }
    }
</style>





