<template>
    <div id= "listbox">
        <div class= "listSearch">
            <form>
                <input type= "text"/>
                <i class="iconfont icon-msnui-find-bold"></i>
            </form>
        </div>
        <ul v-if= "list">
            <li v-for= "(item,i) in list" :key= "i">
                <a href= "javascript:0;">
                    <img :src= "item.img"/>
                </a>
                <div class= "listContent">
                    <h3>{{item.title}}</h3>
                    <p>{{item.brief}}</p>
                    <div class= "listClicks">
                        <div class= "browsebox">
                            <i class="iconfont icon-yanjing"></i>
                            <em>{{item.browse}}</em>
                        </div>
                        <div class= "comments">
                            <span>
                                <i class="iconfont icon-z-likeFill"></i>
                                <em>{{item.Fabulous}}</em>
                            </span>
                            <span>
                                <i class="iconfont icon-z-nolikeFill"></i>
                                <em>{{item.water}}</em>
                            </span>
                        </div>
                    </div>
                </div>
            </li>
        </ul>
        <div v-show= "show" class= "looding">{{looding}}</div>
        <div v-if= "total" v-show= " list.length == total" class= "listFooter">到底了老铁！</div>
    </div>
</template>

<script>
export default {
    name: 'List',
    data () {
        return {
            list:[],
            page:1,
            num:5,
            totalPage:null,
            show:false,
            looding: '正在加载中...',
            total: null
        }
    },
    methods: {
        get(){
            var self = this;
            self.show = true;
            setTimeout(function(){

            
                
                $.ajax({
                    type:'post',
                    url:'http://45.76.169.191:80/getList',
                    data:{
                        page:self.page,
                        num:self.num
                    },
                    success:function(data){ 
                        self.total = JSON.parse(data).total;
                        var totalPage = Math.ceil(JSON.parse(data).total/self.num);
                      
                        self.totalPage = totalPage
                      
                        var data = JSON.parse(data).data;
                    
                        
                        data.map(function(item,index){
                            if(item.img == ''){
                                item.img = 'img/icon.jpg'
                            }
                            self.list.push({
                                id: item.id,
                                title: item.title,
                                brief: item.brief,
                                img: require(`../../../static/${item.img}`),
                                water: item.water,
                                browse: item.browse,
                                Fabulous: item.Fabulous
                            })
                        })

                        self.show = false;
                        
                    }
                })
            },500)
            
        }
        
    },
    mounted () {
        //加载数据
        this.get();
    },

    watch: {
        page :function() {
            this.get();
        }
    }

}
</script>

<style lang="scss" scoped>
    #listbox{
        .listSearch{
            border-bottom:1px solid #ddd;
           form{
               width:65%;
               margin:0 auto;
               padding: 20px 0;
               display: flex;
               input{
                   flex:2.7;
                   height:35px;
                   border-radius: 4px 0 0 4px;
                   border:1px solid #ddd;
                   padding:0 10px;
                   outline: none;
               }
               i{
                   cursor: pointer;
                   display: inline-block;
                   background:$background;
                   color:#fff;
                   flex:0.3;
                   border-radius: 0 4px 4px 0;
                   text-align: center;
                   line-height: 37px;
               }
           }
            
        }
        ul{
            padding:0 20px;
            li{
                cursor: pointer;
                padding:25px 10px;
                clear: both;
                border-bottom:1px solid #ddd;
                overflow: hidden;
                &:last-child{
                    border:0;
                }
                a{
                    display: inline-block;
                    width:30%;
                    height:140px;
                    min-width:100px;
                    float:left;
                    margin-right:20px;
                    border-radius: 4px;
                    overflow: hidden;
                    img{
                        width:100%;
                        height:100%;
                        border:0;
                    }
                }
                .listContent{
                    width:67%;
                    height:140px;
                    color:#545252;
                    float:left;
                    display: flex;
                    flex-flow: column;
                    justify-content: space-between;
                    .listClicks{
                        font-size:12px;
                        .browsebox{
                            float:left;
                            i{
                                float:left;
                                margin-right:5px;
                            }
                        }
                        .comments{
                            float:right;
                            span{
                                display: inline-block;
                                margin-left:10px;
                                 i{
                                    float:left;
                                    margin-right:5px;
                                }
                                em{
                                    float:left;
                                }
                            }
                        }
                    }
                   
                }
                &:hover{
                    background:#f7f7f7;
                    border-radius: 6px;
                }
            }
        }
        .looding{
            position: absolute;
            left:43%;
            top:50%;
            transform: translate(-50%,50%);
            width:150px;
            height:40px;
            line-height: 40px;
            text-align: center;
            background:rgba(0,0,0,.8);
            color:#fff;
            border-radius: 4px;
            font-size:16px;
        }
        .listFooter{
            text-align: center;
            background:rgba(0,0,0,.5);
            color:#cac3c3;
            padding:20px 0;
        }
    }
</style>





