import Vue from 'vue'
import Router from 'vue-router'

import HelloWorld from '@/components/HelloWorld'
import Looding from '@/components/Looding'

import List from '@/components/list/listPage'
import setUp from '@/components/setUp/setUpPage'
import Backstage from '@/components/backstage/Backstage'



Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      redirect: '/list',
      name: 'HelloWorld',
      component: HelloWorld,
      children: [
        {
          path: 'list',
          name: 'List',
          component: List
        },{
          path: 'setUp',
          name: 'setUp',
          component: setUp
        }
      ]
    },{
      path: '/looding',
      name: 'Looding',
      component: Looding,
      children: [
        {
          path: 'backstage',
          name: 'Backstage',
          component: Backstage
        }
      ]
    }
  ]
})



